@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
.cedula{display:flex;flex-direction:column;min-height:calc(100vh - 240px);padding:0 0.5em;max-width:1440px;padding:2em;margin:auto}.cedula .cedula__title{display:flex;justify-content:space-between;align-items:center}.cedula .cedula__title h1{font-size:3em;font-weight:200}.cedula .cedula__title p{font-size:2em;font-weight:400}.cedula .cedula__indicaciones p{font-size:1.5em;font-weight:200}.cedula .lista{max-height:calc(100vh - 450px);overflow-y:auto;display:flex;flex-wrap:wrap;grid-gap:1em;gap:1em;padding:1em}.cedula .lista .card{max-width:calc(50% - 1.5em);cursor:pointer;flex:calc(50% - 1.5em) 1;display:flex;padding:1em;justify-content:space-between;border-radius:8px}@media screen and (max-width: 750px){.cedula .lista .card{max-width:unset;flex:calc(100% - 1.5em) 1}}.cedula .lista .card .card__avatar,.cedula .lista .card .card__logo{display:flex;justify-content:center;align-items:center}.cedula .lista .card .card__avatar img,.cedula .lista .card .card__logo img{max-height:70px;max-width:70px}.cedula .lista .card .card__info{display:flex;flex-direction:column;text-align:center;justify-content:space-evenly}.cedula .lista .card .card__info .card__partido{font-size:14px}.cedula__form{height:80px;display:flex;align-items:flex-start;justify-content:center}.cedula__form button{border:none;outline:none;font-size:1.8em;border-radius:0.2em;padding:0.3em 1em;cursor:pointer;font-weight:200}

.estadisticas{display:flex;flex-direction:column;min-height:calc(100vh - 160px);padding:0 0.5em;max-width:1440px;padding:2em;margin:auto}.estadisticas .estadisticas__title{display:flex;justify-content:space-between;align-items:center}.estadisticas .estadisticas__title h1{font-size:3em;font-weight:200}.estadisticas .estadisticas__indicaciones p{font-size:1.5em;font-weight:200}.estadisticas .estadisticas__chart{margin-top:1em;border-radius:8px;padding:1.5em}

.formulario{display:flex;min-height:calc(100vh - 160px);padding:0 0.5em}.form{font-weight:300;padding:2em;width:600px;border-radius:0.5em;margin:5rem auto}.form>label{margin-bottom:1em}.form>*:not(label){margin-bottom:3em}.form .errores{margin:0;display:flex;flex-direction:column}.form label{display:block;font-size:1.5em}.form>input[type="email"]{width:100%;border:none;outline:none;text-align:center;font-size:1.5em}.form .form__group{display:flex;justify-content:space-between;grid-gap:0.4rem;gap:0.4rem}.form .form__group input[type="text"]{width:2em;border:none;outline:none;text-align:center;font-size:1.5em}.form .form__button{display:flex;justify-content:center}.form .form__button button{border:none;outline:none;font-size:1.8em;border-radius:0.2em;padding:0.3em 1em;cursor:pointer;font-weight:200}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Poppins", sans-serif}.wrapper{max-width:1440px}.header{height:80px;box-shadow:0px 4px 4px #00000040}.header .header__wrapper{max-width:1440px;margin:0 auto;height:100%;display:flex;align-items:center;padding:1em;justify-content:space-between}.header .header__codigo{font-size:2em}.header .header__codigo span:last-of-type{font-weight:500}.header .header__social .header__links{list-style:none;display:flex;grid-gap:1em;gap:1em}.header .header__social .header__links .header__link a{display:flex;justify-content:center;align-items:center;width:2.5em;height:2.5em;border-radius:50%;text-decoration:none}.main{display:flex;flex-direction:column;min-height:calc(100vh - 160px)}.main .main__menu{display:flex;justify-content:center;align-items:center;grid-gap:2em;gap:2em;margin-top:10em}.main .main__circle{height:250px;width:250px;border-radius:50%;display:flex;flex-direction:column;justify-content:center;align-items:center;font-size:1.8em;text-align:center;cursor:pointer;overflow:hidden}.main .main__circle .main__icono{flex:1 1;width:100%;display:flex;align-items:flex-end;justify-content:center}.main .main__circle .main__title{flex:1 1;width:100%;font-weight:200}.main .main__form{display:flex;justify-content:center;margin-top:4em}.main .main__form button{border:none;outline:none;font-size:1.8em;border-radius:0.2em;padding:0.3em 1em;cursor:pointer;font-weight:200}.footer{height:80px;display:flex;justify-content:center;align-items:center;font-weight:300}.cargando{position:absolute;top:0;left:0;width:100vw;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:rgba(40,6,21,0.8);-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px);color:white}.cargando p{font-size:2em;font-weight:300}

body{background-color:#eee}header{background-color:white}header .header__codigo span:first-of-type{color:#252A59}header .header__codigo span:last-of-type{color:#ac1b5a}header .header__social .header__links .header__link a{color:white;background-color:#ac1b5a}.main .main__circle{background-color:#ac1b5a;color:white;box-shadow:0px 0px 20px #bbb}.main .main__form button{background-color:#ac1b5a;color:white}.footer{background-color:#ac1b5a;color:white;font-size:0.8em}.form{background-color:white;box-shadow:0px 0px 20px #bbb}.form label{color:#ac1b5a}.form>input[type="email"]{border-bottom:2px solid #ac1b5a !important;color:#ac1b5a}.form .form__group input[type="text"]{border-bottom:2px solid #ac1b5a !important;color:#ac1b5a}.form .form__button button{background-color:#ac1b5a;color:white}.form small.error{color:red}.cedula .cedula__title h1,.cedula .cedula__title p{color:#ac1b5a}.cedula .cedula__indicaciones p{color:#ac1b5a}.cedula .lista .card{box-shadow:0px 0px 20px #bbb;background-color:white;border-left:8px solid #ac1b5a}.cedula .lista .card .card__info .card__name{color:#444}.cedula .lista .card .card__info .card__partido{color:#888}.cedula .lista .card:hover,.cedula .lista .card.active{background-color:#ac1b5a}.cedula .lista .card:hover .card__name{color:white !important}.cedula .lista .card:hover .card__partido{color:#ddd !important}.cedula__form button{background-color:#ac1b5a;color:white}.cedula__form button[disabled]{background-color:#f7cfe0;cursor:unset}.estadisticas .estadisticas__chart{background-color:white;box-shadow:0px 0px 20px #ccc}.estadisticas .estadisticas__title h1,.estadisticas .estadisticas__title p{color:#ac1b5a}.estadisticas .estadisticas__indicaciones p{color:#ac1b5a}

