.formulario {
	display: flex;
	min-height: calc(100vh - 160px);
	padding: 0 0.5em;
}

.form {
	font-weight: 300;

	padding: 2em;
	width: 600px;
	border-radius: 0.5em;
	margin: 5rem auto;
	& > label {
		margin-bottom: 1em;
	}
	& > *:not(label) {
		margin-bottom: 3em;
	}
	.errores {
		margin: 0;
		display: flex;
		flex-direction: column;
	}
	label {
		display: block;
		font-size: 1.5em;
	}
	& > input[type="email"] {
		width: 100%;
		border: none;
		outline: none;
		text-align: center;
		font-size: 1.5em;
	}
	.form__group {
		display: flex;
		justify-content: space-between;
		gap: 0.4rem;
		input[type="text"] {
			width: 2em;
			border: none;
			outline: none;
			text-align: center;
			font-size: 1.5em;
		}
	}
	.form__button {
		display: flex;
		justify-content: center;
		button {
			border: none;
			outline: none;
			font-size: 1.8em;
			border-radius: 0.2em;
			padding: 0.3em 1em;
			cursor: pointer;
			font-weight: 200;
		}
	}
}
