@import "variables";
body {
	background-color: $fondo-light;
}
header {
	background-color: white;
	.header__wrapper {
		// background-color: #bbb;
	}
	.header__codigo {
		span:first-of-type {
			color: $indigo;
		}
		span:last-of-type {
			color: $magenta;
		}
	}
	.header__social {
		.header__links {
			.header__link {
				a {
					color: white;
					background-color: $magenta;
				}
			}
		}
	}
}

.main {
	.main__menu {
	}
	.main__circle {
		background-color: $magenta;
		color: white;
		box-shadow: 0px 0px 20px #bbb;
	}
	.main__form {
		button {
			background-color: $magenta;
			color: white;
		}
	}
}

.footer {
	background-color: $magenta;
	color: white;
	font-size: 0.8em;
}

.form {
	background-color: white;
	box-shadow: 0px 0px 20px #bbb;
	label {
		color: $magenta;
	}
	& > input[type="email"] {
		border-bottom: 2px solid $magenta !important;
		color: $magenta;
	}
	.form__group {
		input[type="text"] {
			border-bottom: 2px solid $magenta !important;
			color: $magenta;
		}
	}
	.form__button {
		button {
			background-color: $magenta;
			color: white;
		}
	}
	small.error {
		color: red;
	}
}

.cedula {
	.cedula__title {
		h1,
		p {
			color: $magenta;
		}
	}
	.cedula__indicaciones {
		p {
			color: $magenta;
		}
	}

	.lista {
		.card {
			box-shadow: 0px 0px 20px #bbb;
			background-color: white;
			border-left: 8px solid $magenta;
			.card__info {
				.card__name {
					color: #444;
				}
				.card__partido {
					color: #888;
				}
			}

			&:hover,
			&.active {
				background-color: $magenta;
			}
			&:hover .card__name {
				color: white !important;
			}
			&:hover .card__partido {
				color: #ddd !important;
			}
		}
	}
}

.cedula__form {
	button {
		background-color: $magenta;
		color: white;
	}
	button[disabled] {
		background-color: lighten($color: $magenta, $amount: 50%);
		cursor: unset;
	}
}

.estadisticas {
	.estadisticas__chart {
		background-color: white;
		box-shadow: 0px 0px 20px #ccc;
	}
	.estadisticas__title {
		h1,
		p {
			color: $magenta;
		}
	}
	.estadisticas__indicaciones {
		p {
			color: $magenta;
		}
	}
}
