@import "variables";
.estadisticas {
	display: flex;
	flex-direction: column;
	min-height: calc(100vh - 160px);
	padding: 0 0.5em;
	max-width: $ancho;
	padding: 2em;
	margin: auto;
	.estadisticas__title {
		display: flex;
		justify-content: space-between;
		align-items: center;
		h1 {
			font-size: 3em;
			font-weight: 200;
		}
	}
	.estadisticas__indicaciones {
		p {
			font-size: 1.5em;
			font-weight: 200;
		}
	}
	.estadisticas__chart {
		margin-top: 1em;
		border-radius: 8px;
		padding: 1.5em;
	}
}
