@import "variables";
.cedula {
	display: flex;
	flex-direction: column;
	min-height: calc(100vh - 240px);
	padding: 0 0.5em;
	max-width: $ancho;
	padding: 2em;
	margin: auto;
	.cedula__title {
		display: flex;
		justify-content: space-between;
		align-items: center;
		h1 {
			font-size: 3em;
			font-weight: 200;
		}
		p {
			font-size: 2em;
			font-weight: 400;
		}
	}
	.cedula__indicaciones {
		p {
			font-size: 1.5em;
			font-weight: 200;
		}
	}
	.lista {
		max-height: calc(100vh - 450px);
		overflow-y: auto;

		display: flex;
		flex-wrap: wrap;
		gap: 1em;
		padding: 1em;

		.card {
			@media screen and(max-width:750px) {
				max-width: unset;
				flex: calc(100% - 1.5em);
			}
			max-width: calc(50% - 1.5em);
			cursor: pointer;
			flex: calc(50% - 1.5em);
			display: flex;
			padding: 1em;
			justify-content: space-between;
			border-radius: 8px;
			.card__avatar,
			.card__logo {
				display: flex;
				justify-content: center;
				align-items: center;
			}
			.card__avatar img,
			.card__logo img {
				max-height: 70px;
				max-width: 70px;
			}
			.card__info {
				display: flex;
				flex-direction: column;
				text-align: center;
				justify-content: space-evenly;
				.card__partido {
					font-size: 14px;
				}
			}
		}
	}
}
.cedula__form {
	height: 80px;
	display: flex;
	align-items: flex-start;
	justify-content: center;
	button {
		border: none;
		outline: none;
		font-size: 1.8em;
		border-radius: 0.2em;
		padding: 0.3em 1em;
		cursor: pointer;
		font-weight: 200;
	}
}
