@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import "variables";

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Poppins", sans-serif;
}
.wrapper {
	max-width: $ancho;
}

.header {
	height: 80px;
	box-shadow: 0px 4px 4px #00000040;
}

.header {
	.header__wrapper {
		max-width: $ancho;
		margin: 0 auto;
		height: 100%;
		display: flex;
		align-items: center;
		padding: 1em;
		justify-content: space-between;
	}
	.header__codigo {
		font-size: 2em;
		span:last-of-type {
			font-weight: 500;
		}
	}
	.header__social {
		.header__links {
			list-style: none;
			display: flex;
			gap: 1em;
			.header__link {
				a {
					display: flex;
					justify-content: center;
					align-items: center;
					width: 2.5em;
					height: 2.5em;
					border-radius: 50%;
					text-decoration: none;
				}
			}
		}
	}
}

.main {
	display: flex;
	flex-direction: column;
	min-height: calc(100vh - 160px);
	.main__menu {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 2em;
		margin-top: 10em;
	}
	.main__circle {
		height: 250px;
		width: 250px;
		border-radius: 50%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		font-size: 1.8em;
		text-align: center;
		cursor: pointer;
		overflow: hidden;
		.main__icono {
			flex: 1;
			width: 100%;
			display: flex;
			align-items: flex-end;
			justify-content: center;
		}
		.main__title {
			flex: 1;
			width: 100%;
			font-weight: 200;
		}
	}
	.main__form {
		display: flex;
		justify-content: center;
		margin-top: 4em;
		button {
			border: none;
			outline: none;
			font-size: 1.8em;
			border-radius: 0.2em;
			padding: 0.3em 1em;
			cursor: pointer;
			font-weight: 200;
		}
	}
}
.footer {
	height: 80px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: 300;
}

.cargando {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: rgba(
		$color: darken($color: $magenta, $amount: 30%),
		$alpha: 0.8
	);
	backdrop-filter: blur(2px);
	color: white;
	p {
		font-size: 2em;
		font-weight: 300;
	}
}
